.app-nav__link{width:100%;display:flex;align-items:center;justify-content:flex-start;padding:10px 0px 10px 20px;font-size:20px;color:#adadad;text-decoration:none;background-color:#111;border-left:1px dotted #333;box-shadow:0 0 5px #000 inset;transition:all 150ms;font-family:"BebasNeueRegular",Arial,sans-serif}@media screen and (min-width: 630px){.app-nav__link{color:#665;width:25%;height:65px;justify-content:center;padding:0;line-height:65px;font-size:30px}}.app-nav__link:hover{color:#ddd}.app-nav__link:hover .nav__glyph{color:#eabb00}.app-nav__link:hover .cog{transform:rotate(60deg)}.app-nav__link:hover .share{transform:rotate(-45deg)}.app-nav__link:hover .mail{transform:scale(1.2)}.app-nav__link:hover .document_alt_fill{transform:scale(1.2)}.app-nav__link.active{color:#eabb00;cursor:default;background-color:#222;text-shadow:0 1px 3px #000}@media screen and (min-width: 630px){.app-nav__link.active{box-shadow:-8px 0 18px rgba(0,0,0,.7) inset,8px 0 18px rgba(0,0,0,.7) inset}}.app-nav__link.active:hover{color:#eabb00}.app-nav__link.active:hover .nav__glyph{color:#00fffb}.app-nav__link.active .nav__glyph{color:#00fffb}.nav__glyph{font-family:"IconicStroke";display:inline-block;margin-right:15px;position:relative;line-height:1;color:#3a3a3a;font-size:18px;transition:all 150ms}@media screen and (min-width: 630px){.nav__glyph{font-size:30px}}.app-nav__trigger{display:flex;align-items:center;justify-content:center;position:absolute;top:15px;right:20px;z-index:5;padding:8px 12px 4px 12px;border-radius:3px;border:1px solid #353535;background:rgba(0,0,0,.4);cursor:pointer}.app-nav__trigger:hover .nav__glyph{color:#ccc}.app-nav__trigger .nav__glyph{margin:0 !important;color:#888;font-size:36px}.app-nav__trigger--is-active .nav__glyph{color:#ccc}@media screen and (min-width: 630px){.app-nav__trigger{display:none}}.app-nav__wrapper{position:absolute;display:flex;flex-wrap:wrap;width:100%;max-width:1000px;z-index:1;background-color:#000;box-shadow:0 1px 5px #000;transition:transform .3s;transform:translateY(-100%)}.app-nav__wrapper--is-open{transform:translateY(0)}@media screen and (min-width: 1000px){.app-nav__wrapper{left:calc((100vw - 1000px)/2)}}@media screen and (min-width: 630px){.app-nav__wrapper{border:2px solid #333;transform:translateY(0)}}.mail:before{content:"✉"}.document_alt_fill:before{content:""}.cog:before{content:"⚙"}.share:before{content:""}.layers:before{content:""}