@use "/src/styles/mixins";
@use "/src/styles/colors";

.slider__menu {
    display: flex;
    width: 100%;
    align-items: center;
    padding: 4px 0 4px 10px;
    margin-top: 1px;
    background-color: rgba(#000, 0.4);
    box-shadow:
        1px -2px 2px rgba(255, 255, 211, 0.5) inset,
        -3px 2px 3px rgba(0, 0, 0, 0.8) inset;

    @include mixins.breakpointMin(715px) {
        width: auto;
        border-radius: 15px;
    }

    &--play-state {
        width: 55px;
        height: 18px;
        padding: 0 0 5px 0;
        margin-right: 6px;
        font-size: 12px;
        line-height: 15px;
        text-align: center;
        font-style: italic;

        border-radius: 5px 18px 18px 5px;
        color: colors.$gold;
        background-color: rgba(#000, 0.3);
    }
}
